// Widths
$column: 70;
$margin: 15;
$max-width: (1170 + $margin*2);

//1380 wide with 15px margin means $column = 85

// Breakpoints (these area defaults)
// Change per project

$tiny: 420px;
$small: 568px;
$medium: 768px;
$small-medium: 767px;
$med-large: 860px; 
$large: 980px;
$large-huge: 1023px;
$huge: 1200px;
$mega: 1400px;



$base-font-size: 15;
$max-desktop: 1200px; // this is the same as $max-width, but it includes px unit.
$small-desktop: 980px;
$min-desktop: 769px;
$max-tablet: 768px;
$min-tablet: 569px;
$max-mobile: 568px;
