  width: 25px;
  margin-right: 11px; }
  .hero-area-quick-links__list-icon-wrap > img {
    max-height: 22px;
    width: auto; }

.popup-block, .cta-popup {
  background-color: rgba(0, 0, 0, 0.4);
  position: fixed;
  display: none;
  align-items: center;
  justify-content: center; }

.popup-block {
  background-color: rgba(0, 0, 0, 0.4);
  width: 100vw;
  height: 100vh;
  z-index: 100; }
  .popup-block__inner {
    background-color: #FFF;
    padding: 40px;
    text-align: center;
    position: relative;
    margin: 20px; }
    .popup-block__inner--title {
      font-weight: 600;
      font-size: 1.5em; }
    .popup-block__inner--text {
      margin: 20px 0 30px; }
    .popup-block__inner--btn {
      cursor: pointer; }
      .popup-block__inner--btn:hover > span:after {
        transform: scaleX(1); }
    .popup-block__inner--close {
      position: absolute;
      top: -30px;
      right: 0;
      width: 25px;
      height: 25px;
      background-repeat: no-repeat;
      background-size: cover;
      cursor: pointer; }
      .popup-block__inner--close img {
        width: 100%; }
      .popup-block__inner--close:hover .pop-up-fill {
        fill: #FFF; }
  @media (min-width: 769px) {
    .popup-block__inner {
      max-width: 700px;
      padding: 40px 90px; }
      .popup-block__inner--close {
        right: -30px; } }

.cta-popup-links {
  margin-bottom: 30px; }
  .cta-popup-links a {
    margin-bottom: 10px; }

.cta-popup {
  top: 0;
  left: 0;
  z-index: 9999;
  width: 100%;
  bottom: 0; }
  .cta-popup__inner {
    background-color: #E6E6E6;
    max-width: 750px;
    padding: 20px;
    margin: 0 10px; }
  .cta-popup__header {
    display: flex;
    flex-direction: column-reverse;
    margin-bottom: 20px; }
    .cta-popup__header p {
      background-color: #0000FF;
      color: #FFF;
      margin: 0;
      padding: 12px 20px;
      flex-grow: 1;
      font-weight: bold; }
    .cta-popup__header button {
      min-width: 93px;
      margin-bottom: 20px;
      align-self: center; }
  .cta-popup__text {
    background-color: #FFF;
    overflow: auto;
    overflow-x: hidden;
    min-height: 215px;
    max-height: 430px;
    padding: 15px 35px 15px 20px; }
  .cta-popup__footer {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    margin-top: 20px; }
    .cta-popup__footer .checkbox-wrapper label {
      margin-bottom: 0; }
    .cta-popup__footer .checkbox-wrapper .field-validation-error {
      display: none; }
    .cta-popup__footer .button {
      margin-top: 20px;
      min-width: 120px; }
  @media (min-width: 769px) {
    .cta-popup {
      padding: 30px; }
      .cta-popup__header {
        flex-direction: row; }
        .cta-popup__header p {
          margin-right: 20px; }
        .cta-popup__header button {
          margin-bottom: 0; }
      .cta-popup__footer {
        flex-direction: row; }
        .cta-popup__footer .button {
          margin-top: 0; } }

.skip-content {
  position: absolute;
  left: -999em;
  width: auto;
  z-index: 9999; }
  .skip-content:active, .skip-content:focus {
    left: 0; }

.unsupported-browser {
  display: none;
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
  padding: 30px 20px;
  background-color: #FFF;
  box-shadow: 0 -2px 5px 0px rgba(0, 0, 0, 0.1);
  z-index: 2; }

.unsupported-browser-inner {
  max-width: 1200px;
  display: flex;
  justify-content: center;
  margin: 0 auto;
  text-align: center; }
  .unsupported-browser-inner p {
    margin: 0; }

.unsupported-browser-close {
  display: flex;
  align-items: center;
  margin-left: 20px;
  cursor: pointer;
  transition: transform 0.5s cubic-bezier(0.34, 1.56, 0.64, 1); }
  .unsupported-browser-close .icon {
    width: 20px;
    height: 20px; }
  .unsupported-browser-close:hover {
    transform: rotate(90deg); }
